<template>
  <div class="login">
    <div class="loginBox">
      <h2>管理员登陆</h2>
      <div class="item">
        <input type="text" required v-model="codeval" />
        <p>用户名</p>
      </div>
      <!-- <div class="item">
        <input type="password" required v-model="password" />
        <p>密码</p>
      </div> -->
      <div class="jzm_text">
        <div class="item jzm">
          <input type="text" required v-model="jym" />
          <p>校验码</p>
        </div>
        <button class="getJzm" @click="getJym">获取校验码</button>
      </div>

      <button class="btn" @click="login">
        登录
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </button>
    </div>
  </div>
</template>

<script>
import md5 from "md5";
export default {
  name: "login",
  data() {
    return {
      username: "",
      password: "",
      jym: "",
      scJzm: "",
    };
  },
  components: {},
  computed: {
    codeval: {
      get: function () {
        return this.username;
      },
      set: function (val) {
        this.username = val.toUpperCase();
      },
    },
  },
  //   mounted(){},
  methods: {
    onSubmit() {
      return false;
    },
    login() {
      if (this.jym === "") {
        this.$message({
          message: "请输入校验码",
          type: "warning",
        });
      } else if (this.jym !== this.scJzm) {
        this.$message({
          message: "校验码错误",
          type: "error",
        });
      } else if (this.username == "") {
        this.$message({
          message: "请输入用户名",
          type: "warning",
        });
      } else {
        this.$axios.get("/api/config").then((res) => {
          if (res.data.status == -1) {
            this.$message({
              message: "配置文件丢失!",
              type: "error",
            });
          } else {
            let pass = res.data.data.password[0];
            if (md5(this.username) != pass) {
              this.$message({
                message: "用户名错误！",
                type: "error",
              });
            } else {
              this.$message({
                message: "登陆成功！",
                type: "success",
              });
              localStorage.isLogin = true;
              localStorage.username = md5(this.username);
              localStorage.page = 1;
              setTimeout(() => {
                const target = this.$route.query.target;
                this.$router.push({ path: "/index", replace: true });
              }, 1000);
            }
          }
        });
      }
    },
    getJym() {
      this.scJzm = "";
      const arr =
        "qwertyuipasdfghjklzxcvbnm123456789QWERTYUIOPASDFGHJKLZXCVBNM";
      for (let i = 0; i < 4; i++) {
        const text = Math.round(Math.random() * arr.length);
        this.scJzm += arr[text];
      }
    //   console.log(this.scJzm);
      this.jym = this.scJzm;
    },
  },
};
</script>
<style scoped>
@import "../static/login.css";
</style>
